import React from 'react';
import classes from './Block.module.css';

const Block = (props) => {

    let newListElement = React.createRef();

    let addList = () => {
        let text = newListElement.current.value;
        props.addList(text)
    }

    let delDiv = () => {
        props.delDiv()
    }

    
    const CrList = (props) => {
        console.log(props.delDiv)
        return (
            <div className={classes.remuvList}>
             {props.valueL}
             <button onClick={delDiv}>del</button>
            </div>
        )
    }

    let crList = props.dataList.map( lc => <CrList valueL={lc.valueL} dataList={props.dataList} delDiv={props.delDiv} />)


    return (
        <div className={classes.box}>
           <div className={classes.block}>
               {props.title}
               <textarea ref={newListElement} className={classes.listVal}></textarea>
               <button onClick={addList}>add</button>
           </div>
           {crList}
        </div>
    )
}

export default Block;