import React from 'react';
import {rerenderEntireTree} from './../render';

let state = {
    dataBlock : [
        {id: 1, title: "list 1"},
        {id: 2, title: "list 2"},
        {id: 3, title: "list 3"}
      ],
    dataList : []
}

export let addList = (someList) => {
 
    let newList = {
        valueL : someList,
    }
    state.dataList.push(newList);
    let listVal = document.getElementsByClassName("listVal");
    listVal.value = ''
    rerenderEntireTree(state)
}

export let delDiv = () => {
    let remL = document.getElementsByClassName("removList");
    remL.addClass("active");
    let remD = document.getElementsByClassName('box');
    remD.remove('remuvList')

}

export default state;